<template>
	<view class="nlp-page">

		<view class="nlp-form">
			<view class="form-wrapper">
				<u-form :model="dataForm" ref="uForm">
					<u-form-item label="推送类型" prop="type" label-width="150rpx" :key="1">
						<u-radio-group v-model="dataForm.type">
							<u-radio label="微信"  :name="1" >微信</u-radio>
							<u-radio label="优理宝" :name="2">优理宝</u-radio>
							<u-radio label="抖音" :name="3" >抖音</u-radio>
						</u-radio-group>
					</u-form-item>
					<u-form-item v-if="dataForm.type==1" label="微信标题" prop="wxTitle" label-width="150rpx" :key="2">
						<u-input v-model="dataForm.wxTitle" placeholder="" />
					</u-form-item>
					<u-form-item v-if="dataForm.type==1" label="微信内容" prop="wxKeyword1" label-width="150rpx" :key="3">
						<u-input v-model="dataForm.wxKeyword1" placeholder="" />
					</u-form-item>
					<u-form-item v-if="dataForm.type==1" label="备注" label-width="150rpx" :key="4">
						<u-input v-model="dataForm.wxRemark" placeholder="" />
					</u-form-item>
					<u-form-item v-if="dataForm.type==2" label="优理宝标题" prop="appTitle" label-width="150rpx" :key="5">
						<u-input v-model="dataForm.appTitle" placeholder="" />
					</u-form-item>
					<u-form-item v-if="dataForm.type==2" label="优理宝简述" prop="appDesc" label-width="150rpx" :key="6">
						<u-input v-model="dataForm.appDesc" placeholder="" />
					</u-form-item>
					<u-form-item v-if="dataForm.type==2" label="优理宝内容" prop="payload" label-width="150rpx" :key="7">
						<u-input v-model="dataForm.payload" placeholder="" />
					</u-form-item>
					<u-form-item v-if="dataForm.type==2" label="优理宝封面" label-width="150rpx" :key="8">
						<u-input v-model="dataForm.pictureUrlList" placeholder="" />
					</u-form-item>
					<u-form-item v-if="dataForm.type==2" label="跳转链接" label-width="150rpx" :key="9">
						<u-input v-model="dataForm.appLinkUrl" placeholder="" />
					</u-form-item>
					<u-form-item v-if="dataForm.type==3" label="抖音活动名称" prop="dyKeyword1" label-width="180rpx"
						:key="10">
						<u-input v-model="dataForm.dyKeyword1" placeholder="" />
					</u-form-item>
					<u-form-item v-if="dataForm.type==3" label="抖音通知详情" prop="dyKeyword2" label-width="180rpx" :key="11">
						<u-input v-model="dataForm.dyKeyword2" placeholder="" />
						<!-- <uni-datetime-picker
						                type="datetime"
						                v-model="dataForm.dyKeyword2"
						                @change="confirmCalendarHandle"
						            /> -->
						<!-- <u-input v-model="dataForm.dyKeyword2"  @focus="showCalendarHandle" placeholder="" /> -->
					</u-form-item>
					<u-form-item v-if="dataForm.type==3" label="抖音备注" label-width="180rpx" :key="12">
						<u-input v-model="dataForm.dyKeyword3" placeholder="" />
					</u-form-item>
				</u-form>
				<u-button @click="queryHandle()" :loading="isLoading" loadingText="加载中" class="login-btn">提交</u-button>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		mapState,
		mapMutations
	} from 'vuex';
	export default {
		data() {
			return {
				showCalendar:true,
				isLoading:false,
				extParams: {},
				dataForm: {
					"type":1,
					//微信
					"wxMsgType": "41153", //微信模板编号，MOT后台配置后返回模板编号ID
					"wxTitle": "",
					"wxLinkUrl": "1", //写死
					"wxKeyword1": "",
					"wxRemark": "",
					"wxcls1": "1", //栏目号1      //写死
					"wxcls2": "2", //栏目号2   
					//优理宝
					"appMsgType": "3_1_132", // app模板 写死
					"appLinkurl": "1", //跳转链接           //写死
					"appTitle": "", //标题
					"appDesc": "", //简述
					"appPayload": "", //内容
					"appcls1": "2", //栏目号1
					"appcls2": "110", //栏目号2
					"appPictureUrlList": "1", //封面图片
					//抖音
					"dyMsgType": "8_1_2", //抖音模板编号，MOT后台配置后返回模板编号ID
					"dyKeyword1": "", //活动名称
					"dyKeyword2": "", //通知详情
					"dyKeyword3": "", //备注
					"dyPage": "1" //跳转链接
				},
				rules: {
					wxTitle: [{
						required: true,
						message: '标题不能为空',
						trigger: 'blur',
					}],
					wxKeyword1: [{
						required: true,
						message: '内容不能为空',
						trigger: 'blur'
					}],
					appTitle: [{
						required: true,
						message: '标题不能为空',
						trigger: 'blur',
					}],
					appDesc: [{
						required: true,
						message: '简述不能为空',
						trigger: 'blur'
					}],
					appPayload: [{
						required: true,
						message: '内容不能为空',
						trigger: 'blur'
					}],
					dyKeyword1: [{
						required: true,
						message: '活动名称不能为空',
						trigger: 'blur',
					}],
					dyKeyword2: [{
						required: true,
						message: '活动详情不能为空',
						trigger: 'blur'
					}],
				}
			}
		},
		computed: mapState([ 'hasLogin','uerInfo2']),
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		},
		onLoad() {

		},

		methods: {
			queryHandle() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						this.isLoading = true
						let params = {}
						let pushXUID = {}
						this.extParams = {
							"trgtClientId":Date.parse(new Date()), 
							"trgtClientType":"2"
						}
						let mobile = this.uerInfo2.mobile 
						if(this.dataForm.type == 1){//微信
							params = {
								"wxMsgType": this.dataForm.wxMsgType, 
								"wxTitle": this.dataForm.wxTitle,
								"wxLinkUrl": this.dataForm.wxLinkUrl, 
								"wxKeyword1": this.dataForm.wxKeyword1,
								"wxRemark": this.dataForm.wxRemark,
								"wxcls1": this.dataForm.wxcls1,
								"wxcls2": this.dataForm.wxcls2, 
							}
							pushXUID.wechat3 = mobile
							this.extParams.wxTemplate = params
						}else if(this.dataForm.type == 2){//优理宝
							params = {
								"appMsgType":this.dataForm.appMsgType ,
								"appLinkUrl":this.dataForm.appLinkurl , //跳转链接           //写死
								"appTitle":this.dataForm.appTitle , //标题
								"appDesc":this.dataForm.appDesc , //简述
								"appPayload":this.dataForm.appPayload , //内容
								"appcls1":this.dataForm.appcls1 , //栏目号1
								"appcls2":this.dataForm.appcls2 , //栏目号2
								"appPictureUrlList":this.dataForm.appPictureUrlList , //封面图片
							}
							pushXUID.pushClientId = mobile
							this.extParams.appTemplate = params
						}else if(this.dataForm.type == 3){//抖音
							params = {
								"dyMsgType": this.dataForm.dyMsgType, //抖音模板编号，MOT后台配置后返回模板编号ID
								"dyKeyword1":this.dataForm.dyKeyword1, //活动名称
								"dyKeyword2":this.dataForm.dyKeyword2, //活动时间
								"dyKeyword3":this.dataForm.dyKeyword3, //备注
								"dyPage": this.dataForm.dyPage //跳转链接
							}
							pushXUID.tiktokMobile = mobile
							this.extParams.douyinTemplate = params
						}
						
						this.request({
							url: "/xuid/SingleNotification",
							method:'post',
							data: Object.assign({
								phone:mobile,
								url:'http://192.25.105.59:10500/usrsub/user/update/',
								extParams:JSON.stringify(this.extParams), 
								pushXUID:JSON.stringify(pushXUID), 
							}),
						}).then(data => {
							this.isLoading = false
							if (data && data.resphead && data.resphead.succeed) {
								uni.showToast({
									title: "发送消息成功",
									icon: "none"
								});
							} else {
								uni.showToast({
									title: data.resphead.message || "发送消息失败",
									icon: "none"
								});
							}
						});
					}
				});
			},
			confirmCalendarHandle(e){
				console.log('dd',e)
				this.dataForm.dyKeyword2 = e[0]
			},
			showCalendarHandle(){
				console.log('ddddddd')
				this.showCalendar = true
			}

		}
	}
</script>
<style scoped>
	.nlp-form {
		padding: 40rpx;
	}
	/deep/.uni-date-editor--x .uni-date__icon-clear{
		top: -3px;
	}
</style>
